import {OBJECT_REQUEST, OBJECT_FAIL, OBJECT_SUCCESS} from "../constants/Object";
import ServerIo from "./../server/ServerIo";
//import {browserHistory} from "react-router";

export function loadObject(payload, token) {

  return (dispatch) => {
    dispatch({
      type: OBJECT_REQUEST
    })
    var serverIo = new ServerIo(token);
    var loadFun = serverIo.getObj({id: payload.id, contentCode: payload.contentCode});
    loadFun.done(data=> {
      dispatch({
        type: OBJECT_SUCCESS,
        payload: {
          data: data
        }
      })
    }).fail(()=> {
      dispatch({
        type: OBJECT_FAIL,
        payload: {}
      })
    });
  }
}


export function createObject(payload, token) {
  return (dispatch) => {
    dispatch({
      type: OBJECT_REQUEST
    })
    var serverIo = new ServerIo(token);
    var loadFun = loadFun = serverIo.getEmptyObj({
      contentCode: payload.contentCode,
      backLinkAttr: payload.backLinkAttr,
      objId: payload.objId
    });
    loadFun.done(data=> {
      dispatch({
        type: OBJECT_SUCCESS,
        payload: {
          data: data
        }
      })
    }).fail(()=> {
      dispatch({
        type: OBJECT_FAIL,
        payload: {}
      })
    });
  }
}



