import React, {Component} from "react";
import {Link} from "react-router";

export default class LinkAttr extends Component {

  constructor(props) {
    super(props);
  }
  
  
  

  render() {
    var value = this.props.value;
    if (value != null) {
      return <Link to={'/obj/'+value.contentCode+'/'+ value.id}>{value.title}</Link>
    } else {
      return <div></div>
    }
  }
}









