import {
  OBJECT_REQUEST,
  OBJECT_FAIL,
  OBJECT_SUCCESS,
  OBJECT_DELETE
} from '../constants/Object';


const initialState = {objData: null}
export default function object(state = initialState, action) {
  switch (action.type) {
    case OBJECT_REQUEST:
      return {...state, objData: null};
    case OBJECT_SUCCESS:
      return {...state, objData: action.payload.data};//
    case OBJECT_DELETE:
      return {...state, objData: null};//
    case OBJECT_FAIL:
      return state;
    default:
      return state;
  }
}



