import $ from "jquery";
import uuid from "uuid-js";

export default class ServerIo {


  constructor(token) {
    this.token = token;
  }


  getObjList(data) {
    var sort = null;
    if (data.sortColumn != null) {
      sort = "&sortColumn=" + data.sortColumn + "&sortDirection=" + data.sortDirection;
    }
    return this.getData('/rest/pr/queryservices/obj/' + data.contentCode + '?offset=' + data.offset + '&limit=' + data.limit + (sort != null ? sort : ''));
  }


  getObj(data) {
    return this.getData('/rest/pr/queryservices/obj/' + data.contentCode + '/' + data.id);
  }

  getEmptyObj(data) {
    return this.getData('/rest/pr/queryservices/newObj/' + data.contentCode + '/' + data.backLinkAttr + '/' + data.objId);
  }

  updateObj(data) {
    return this.putData(data.attrs, '/rest/pr/queryservices/obj/' + data.contentCode + '/' + data.objId);
  }

  createObj(data) {
    return this.sendData(data.attrs, '/rest/pr/queryservices/obj/' + data.contentCode + '/');
  }


  deleteObj(data) {
    return this.deleteData('/rest/pr/queryservices/obj/' + data.contentCode + '/' + data.objId);
  }

  getNav() {
    return this.getData('/rest/pr/queryservices/nav');
  }


  getAuth(data) {
    return this.sendDataSync(data, '/rest/pb/login/login');
  }

  sendFile(data) {
    return $.ajax({
      type: 'POST',
      url: '/rest/pr/upload/uploadFile',
      data: data,
      cache: false,
      enctype: 'multipart/form-data',
      headers: {
        'myresttoken': this.token
      },
      processData: false,
      contentType: false
    })
  }


  submitCommand(command, data) {
    var dataCommand = {
      id: uuid.create().hex,
      type: command,
      title: '',
      descriprion: '',
      object: data
    }
    return this.sendData(dataCommand, '/rest/pr/commandservice/submitCommand');
  }

  deleteData(url) {
    return $.ajax({
      type: 'DELETE',
      url: url,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  putData(data, url) {
    return $.ajax({
      type: 'PUT',
      url: url,
      data: data != null ? JSON.stringify(data) : null,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  getData(url) {
    return $.ajax({
      type: 'GET',
      url: url,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  sendData(data, url) {
    return $.ajax({
      type: 'POST',
      url: url,
      data: data != null ? JSON.stringify(data) : null,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  sendDataSync(data, url) {
    return $.ajax({
      type: 'POST',
      url: url,
      data: data != null ? JSON.stringify(data) : null,
      headers: {
        'myresttoken': this.token
      },
      async: 'false',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }
}



