import React, {Component} from "react";
import {Link} from "react-router";
import Panel from "react-bootstrap/lib//Panel";
import Table from "react-bootstrap/lib/Table";

export default class BackLinkTable extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    var value = this.props.value;
	if(value==null){
		return <div></div>
	}
    return <Panel header=""><Table striped bordered condensed hover>
      <thead>
      <tr>
        {value.columnsView!=null?value.columnsView.map((obj, index)=>
          <th key={index}>
            {obj.name}
          </th>
        ):null}
      </tr>
      </thead>
      <tbody>
      {value.objectsView!=null?value.objectsView.map((obj, index)=>
        <tr key={index}>
          {obj.data.map((row, index2)=>
            <td key={index2}>
              {typeof row == 'string' ? row : ""}
            </td>
          )}
          <tr >
            <Link to={'/obj/'+value.clazz+'/'+ obj.id}>Открыть</Link>
          </tr>
        </tr>
      ):null}
      <tr>
        <td>
          <Link
            to={'/new/'+value.clazz+'/'+value.backLinkAttr+':'+this.props.objId }>Создать</Link>
        </td>
      </tr>
      </tbody>
    </Table>
    </Panel>
  }
}





